"use client";

import React, { useEffect, useState } from "react";
import axios from "axios";
import BackButton from "../../UI/BackButton";
import { baseURL } from "@/Service/axios";
import {
  AreaChart,
  Area,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
  CartesianGrid,
} from "recharts";

/* =====================================================
   CONSTANTS & HELPERS
===================================================== */

const TABS = ["about", "financials", "gmp", "news"];

const allocationDetails = [
  "Total Shares Offered",
  "QIB Shares Offered",
  "‚àí Anchor Investor Shares Offered",
  "NII (HNI) Shares Offered",
  "Retail Shares Offered",
  "Shareholders Shares Offered",
];

const formatIpoDetailKey = (key = "") =>
  key
    .replace(/‚àí\s*/g, "‚Ü≥ ")
    .replace(/QIB \(Ex\. Anchor\)/, "‚Ü≥ QIB (Non-Anchor)")
    .replace(/\u003E/g, ">")
    .replace(/\u003C/g, "<")
    .trim();

/* =====================================================
   SMALL REUSABLE COMPONENTS
===================================================== */

const Section = ({ title, children }) => (
  <section className="p-6 rounded-xl border bg-white dark:bg-slate-900 dark:border-slate-800 shadow-sm">
    {title && (
      <h2 className="text-xl font-semibold mb-4 text-slate-900 dark:text-white">
        {title}
      </h2>
    )}
    {children}
  </section>
);

const InfoCard = ({ label, value }) => (
  <div className="text-center">
    <p className="text-xs text-slate-500">{label}</p>
    <p className="text-lg font-bold mt-1">{value || "-"}</p>
  </div>
);

const FAQItem = ({ question, answer }) => {
  const [open, setOpen] = useState(false);

  return (
    <div className="border rounded-lg overflow-hidden">
      <button
        onClick={() => setOpen(!open)}
        className="w-full p-4 flex justify-between text-left font-medium"
      >
        {question}
        <span className={`transition ${open ? "rotate-180" : ""}`}>‚ñº</span>
      </button>

      {open && (
        <div className="px-4 pb-4 text-sm text-slate-600">{answer}</div>
      )}
    </div>
  );
};

/* =====================================================
   MAIN COMPONENT
===================================================== */

const IpoDetailView = ({ goBack, apiReponse, gmpApiResponse, id, slug }) => {
  const [tab, setTab] = useState("about");
  const [newsData, setNewsData] = useState({});

  const ipo = apiReponse?.data || {};
  const gmpData = gmpApiResponse?.data || {};

  /* ================= FETCH NEWS ================= */
  useEffect(() => {
    if (tab !== "news") return;

    axios
      .get(`${baseURL}/ipo/news/${slug}`)
      .then((res) => setNewsData(res.data))
      .catch(console.error);
  }, [tab, slug]);

  /* =====================================================
     RENDER
  ===================================================== */

  return (
    <section className="py-12 bg-slate-50 dark:bg-slate-950">
      <div className="max-w-6xl mx-auto px-4">

        {/* BACK */}
        <BackButton onClick={goBack} />

        {/* =====================================================
           HEADER
        ===================================================== */}
        <header className="flex gap-4 items-center border-b pb-4 mt-4">
          <img
            src={gmpData.logo || "/no-logo.png"}
            className="w-20 h-20 rounded-full border"
            alt="logo"
          />

          <div>
            <h1 className="text-3xl font-bold">
              {gmpData?.matched_item?.name}
            </h1>

            <p className="text-sm text-slate-500">
              {gmpData?.matched_item?.category} IPO ‚Ä¢{" "}
              {gmpData?.matched_item?.status_text}
            </p>
          </div>
        </header>

        {/* =====================================================
           QUICK INFO
        ===================================================== */}
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mt-6 p-4 border rounded-xl bg-white">
          <InfoCard label="Price Band" value={ipo?.ipoDetails?.["Price Band"]} />
          <InfoCard label="Lot Size" value={ipo?.ipoDetails?.["Lot Size"]} />
          <InfoCard label="GMP" value={gmpData?.current_gmp || "-"} />
          <InfoCard label="Issue Size" value={ipo?.ipoDetails?.["Total Issue Size"]} />
        </div>

        {/* =====================================================
           TABS
        ===================================================== */}
        <div className="flex gap-6 border-b mt-10">
          {TABS.map((t) => (
            <button
              key={t}
              onClick={() => setTab(t)}
              className={`pb-3 font-semibold ${
                tab === t
                  ? "border-b-2 border-emerald-500 text-emerald-600"
                  : "text-slate-500"
              }`}
            >
              {t.toUpperCase()}
            </button>
          ))}
        </div>

        {/* =====================================================
           TAB CONTENT
        ===================================================== */}

        {/* ABOUT */}
        {tab === "about" && (
          <div className="space-y-6 mt-6">
            <Section title={`About ${gmpData?.matched_item?.name}`}>
              <p className="text-sm leading-relaxed">
                This IPO is a {gmpData?.matched_item?.category} offering with
                price band {ipo?.ipoDetails?.["Price Band"]}.
              </p>
            </Section>

            <Section title="FAQs">
              <div className="space-y-3">
                <FAQItem
                  question="What is the GMP?"
                  answer={`Current GMP is ${gmpData?.current_gmp}`}
                />
              </div>
            </Section>
          </div>
        )}

        {/* GMP */}
        {tab === "gmp" && (
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-6 mt-8">
            <Section title="GMP Trend" className="lg:col-span-2">
              <ResponsiveContainer width="100%" height={300}>
                <AreaChart data={gmpData?.chart || []}>
                  <CartesianGrid strokeDasharray="3 3" />
                  <XAxis dataKey="date" />
                  <YAxis />
                  <Tooltip />
                  <Area dataKey="gmp" stroke="#06b6d4" fill="#06b6d4" />
                </AreaChart>
              </ResponsiveContainer>
            </Section>
          </div>
        )}

        {/* NEWS */}
        {tab === "news" && (
          <div className="grid grid-cols-1 sm:grid-cols-2 gap-5 mt-6">
            {(newsData?.news || []).map((n, i) => (
              <Section key={i}>
                <h3 className="font-semibold">{n.name}</h3>
                <p className="text-sm mt-2">{n.description}</p>
              </Section>
            ))}
          </div>
        )}

        {/* DISCLAIMER */}
        <div className="mt-12 p-6 border border-red-200 bg-red-50 rounded-xl">
          <p className="text-sm text-red-700">
            Disclaimer: IPO investments involve risk. GMP is unofficial.
          </p>
        </div>
      </div>
    </section>
  );
};

export default IpoDetailView;
